<template>
  <div>
    <h2>订单统计</h2>
    <div class="block">
      <span class="demonstration">选择时间</span>
      <el-date-picker v-model="value1"
                      type="datetimerange"
                      range-separator="至"
                      start-placeholder="开始日期"
                      end-placeholder="结束日期">
      </el-date-picker>
      <el-button type="primary"
                 @click="clickQuery"
                 style="margin-left:15px">
        查询
      </el-button>
    </div>
    <div id="infoEcharts"></div>
  </div>
</template>

<script>
import { ordertotal } from '@/API/apis'//获取账号列表
import { getchtime2 } from '@/utils/gettime'//时间
import echarts from 'echarts'
export default {
  mounted () {
    this.MYcharts = echarts.init(document.querySelector("#infoEcharts"))
  },
  data () {
    return {
      orderTime: "",
      orderAmount: "",
      value1: ["2019-10-05 00:00:00", getchtime2(new Date())],
    }
  },
  methods: {
    clickQuery () {
      this.value1[0] = getchtime2(this.value1[0])
      this.value1[1] = getchtime2(this.value1[1])
      this.value1 = JSON.stringify(this.value1)
      //接口
      if (this.value1[0] == "" || this.value1[1] == "") {
        this.$message.error('请选择正确的时间!!!');
        return
      } else {
        ordertotal(this.value1).then(res => {
          for (const objs of res.data.data) {
            objs.orderTime = getchtime2(objs.orderTime)
            var DATA = res.data.data.map((item) => {
              return item.orderTime
            })
            var DATA2 = res.data.data.map((item) => {
              return item.orderAmount
            })
          }
          let option = {
            title: {
              text: "数据统计"
            },
            tooltip: {
              trigger: "axis"
            },
            legend: {
              data: ["订单金额"]
            },
            grid: {
              left: "3%",
              right: "4%",
              bottom: "3%",
              containLabel: true
            },
            toolbox: {
              feature: {
                saveAsImage: {}
              }
            },
            xAxis: {
              type: "category",
              boundaryGap: false,
              data: DATA //横轴
            },
            yAxis: {
              type: "value"
            },
            series: [
              {
                name: "订单金额",
                type: "line",
                stack: "总量",
                data: DATA2
              }
            ]
          };
          this.MYcharts.setOption(option);
        })
      }
    }
  }

}

</script>

<style lang="less" scoped>
#infoEcharts {
  width: 100%;
  height: 500px;
}
</style>